*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --color-bg: #ffffff;
}

html {
  height: 100%;
}

body {
  height: 100%;
  min-height: 100vh;
  font-size: 20px;
  font-family: 'Segoe UI', sans-serif;
  background-color: var(--color-bg);
}
